import React, { useState } from 'react';
import { generatePDF } from '../utils/generatePDF';

const statusBadge = (status) => {
  switch (status) {
    case '–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞':
      return <span className="badge bg-secondary">–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞</span>;
    case '–†–µ–º–æ–Ω—Ç':
      return <span className="badge bg-warning text-dark">–†–µ–º–æ–Ω—Ç</span>;
    case '–ì–æ—Ç–æ–≤–æ':
      return <span className="badge bg-success">–ì–æ—Ç–æ–≤–æ</span>;
    default:
      return <span className="badge bg-light text-dark">–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–æ</span>;
  }
};

const OrderCard = ({ order, onUpdate, onDelete }) => {
  const [equipmentState, setEquipmentState] = useState(order.equipment);
  const [editingIndex, setEditingIndex] = useState(null);

  const startEditing = (index) => {
    setEquipmentState(order.equipment);
    setEditingIndex(index);
  };

  const updateEquipmentField = (index, field, value) => {
    const updated = [...equipmentState];
    updated[index] = { ...updated[index], [field]: value };

    if (field === 'warranty' && value) {
      updated[index].repairCost = '';
      updated[index].repairDetails = '';
    }

    setEquipmentState(updated);
  };

  const handleSave = (index) => {
    const updatedOrder = {
      ...order,
      equipment: equipmentState,
    };
    onUpdate(updatedOrder);
    setEditingIndex(null);
  };

  const handleDeleteWithPIN = () => {
    const pin = prompt('–í–≤–µ–¥–∏—Ç–µ PIN-–∫–æ–¥ –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è:');
    if (pin === '0000') {
      onDelete();
    } else {
      alert('–ù–µ–≤–µ—Ä–Ω—ã–π PIN-–∫–æ–¥. –£–¥–∞–ª–µ–Ω–∏–µ –æ—Ç–º–µ–Ω–µ–Ω–æ.');
    }
  };

  const getTotalCost = () =>
    order.equipment.reduce((sum, eq) => {
      if (eq.status === '–ì–æ—Ç–æ–≤–æ' && !eq.warranty) {
        return sum + Number(eq.repairCost || 0);
      }
      return sum;
    }, 0);

  return (
    <div className="card shadow-sm mb-4 border-0">
      <div className="card-body">
        <div className="d-flex justify-content-between align-items-start">
          <div>
  <h5 className="mb-1">
    <i className="bi bi-person-fill me-2"></i>{order.clientName}
  </h5>
  
  {order.clientPhone && (
    <p className="mb-1 text-muted">
      <i className="bi bi-telephone me-1"></i>{order.clientPhone}
    </p>
  )}

  {order.city && (
    <p className="mb-1 text-muted">
      <i className="bi bi-geo-alt me-1"></i>{order.city}
    </p>
  )}

  {order.technician && (
    <p className="mb-1 text-muted">
      <i className="bi bi-person-badge me-1"></i>–¢–µ—Ö–Ω–∏–∫: {order.technician}
    </p>
  )}

  <small className="text-muted">
    <i className="bi bi-clock me-1"></i>
    {order.createdAt
      ? new Date(order.createdAt).toLocaleString('ru-RU')
      : '–î–∞—Ç–∞ –Ω–µ —É–∫–∞–∑–∞–Ω–∞'}
  </small>
</div>


          <div className="d-flex flex-column align-items-end gap-1">
            <button className="btn btn-sm btn-outline-success" onClick={() => generatePDF(order)}>
              <i className="bi bi-download me-1"></i>–°–∫–∞—á–∞—Ç—å –ê–í–†
            </button>
            <button className="btn btn-sm btn-outline-danger" onClick={handleDeleteWithPIN}>
              <i className="bi bi-trash me-1"></i>–£–¥–∞–ª–∏—Ç—å
            </button>
          </div>
        </div>

        <hr />

        <h6 className="mb-3">
          <i className="bi bi-tools me-2"></i>–û–±–æ—Ä—É–¥–æ–≤–∞–Ω–∏–µ
        </h6>

        {order.equipment.map((eq, index) => (
          <div key={index} className="border rounded p-3 mb-3 bg-light-subtle">
            <div className="d-flex justify-content-between">
              <div>
                <div><strong>–¢–∏–ø:</strong> {eq.customType || eq.type}</div>
                <div><strong>–°–µ—Ä–∏–π–Ω—ã–π:</strong> {eq.serial}</div>
              </div>

              <div className="text-end">
                {editingIndex === index ? (
                  <>
                    <select
                      className="form-select form-select-sm mb-2"
                      value={equipmentState[index].status}
                      onChange={(e) => updateEquipmentField(index, 'status', e.target.value)}
                    >
                      <option value="–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞">–î–∏–∞–≥–Ω–æ—Å—Ç–∏–∫–∞</option>
                      <option value="–†–µ–º–æ–Ω—Ç">–†–µ–º–æ–Ω—Ç</option>
                      <option value="–ì–æ—Ç–æ–≤–æ">–ì–æ—Ç–æ–≤–æ</option>
                    </select>

                    {equipmentState[index].status === '–ì–æ—Ç–æ–≤–æ' && (
                      <>
                        <div className="form-check mb-2">
                          <input
                            type="checkbox"
                            className="form-check-input"
                            id={`warranty-${index}`}
                            checked={equipmentState[index].warranty || false}
                            onChange={(e) =>
                              updateEquipmentField(index, 'warranty', e.target.checked)
                            }
                          />
                          <label htmlFor={`warranty-${index}`} className="form-check-label">
                            –ì–∞—Ä–∞–Ω—Ç–∏–π–Ω—ã–π —Ä–µ–º–æ–Ω—Ç
                          </label>
                        </div>

                        {!equipmentState[index].warranty && (
                          <>
                            <input
                              type="text"
                              className="form-control form-control-sm mb-1"
                              placeholder="–£—Å–ª—É–≥–∞"
                              value={equipmentState[index].repairDetails || ''}
                              onChange={(e) =>
                                updateEquipmentField(index, 'repairDetails', e.target.value)
                              }
                            />
                            <input
                              type="number"
                              className="form-control form-control-sm mb-2"
                              placeholder="–°—É–º–º–∞"
                              value={equipmentState[index].repairCost || ''}
                              onChange={(e) =>
                                updateEquipmentField(index, 'repairCost', e.target.value)
                              }
                            />
                          </>
                        )}
                      </>
                    )}

                    <div className="d-flex gap-2 justify-content-end">
                      <button className="btn btn-sm btn-success" onClick={() => handleSave(index)}>
                        <i className="bi bi-check-circle"></i>
                      </button>
                      <button className="btn btn-sm btn-secondary" onClick={() => setEditingIndex(null)}>
                        <i className="bi bi-x-circle"></i>
                      </button>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="mb-1">{statusBadge(eq.status)}</div>
                    {eq.status === '–ì–æ—Ç–æ–≤–æ' && (
                      <>
                        <div><strong>–£—Å–ª—É–≥–∞:</strong> {eq.repairDetails || '‚Äî'}</div>
                        <div>
                          <strong>–°—Ç–æ–∏–º–æ—Å—Ç—å:</strong>{' '}
                          {eq.warranty ? '–ì–∞—Ä–∞–Ω—Ç–∏—è' : `${eq.repairCost || 0} ‚Ç∏`}
                        </div>
                      </>
                    )}
                    <button
                      className="btn btn-sm btn-outline-primary mt-2"
                      onClick={() => startEditing(index)}
                    >
                      <i className="bi bi-pencil"></i>
                    </button>
                  </>
                )}
              </div>
            </div>
          </div>
        ))}

        <div className="text-end mt-2">
          <strong>–ò—Ç–æ–≥–æ: {getTotalCost()} ‚Ç∏</strong>
        </div>
      </div>
    </div>
  );
};

export default OrderCard;
